import {getField} from './fields/fieldMapping.js'

export default {
  name: 'GenericInput',
  functional: true,
  props: {
    name: {},
    type: {
      type: String, // editor
      default: 'string'
    },
    value: {},  // 
    static: {
      type: Boolean,
      default: false
    }, //
    options: Object, // 
    multiple: {
      type: Boolean,
      default: false
    },
    convert: {},
    placeholder: {},
    info: {},
    required: {
      type: Boolean,
      default: false
    },
    display: {
      type: String,
      default: ''
    },
    staticSuffix: {
      type: String,
      default: '_static'
    }
  },
  render (h, ctx) {
    let self = ctx.props
    let InputClass = getField(self.type)
    let input = new InputClass(ctx.props)
    if (self.static) {
      let callback = (v) => {
        ctx.listeners['on-display-change'] && ctx.listeners['on-display-change'](v)
      }
      //name_staticgetStaticValue
      let v = self.value[`${self.name}${self.staticSuffix}`]
      if (!v) {
        input.getStaticValue(self.value[self.name], callback)
      } else {
        callback(v)
      }
    }
    return input.render(h, ctx)
  }
}



